* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  overflow: hidden;
}

body {
  color: #000;
  background-color: #faebd7;
}

.wrapper {
  height: 100vh;
  width: 100vw;
  background-color: #dc143c;
  justify-content: center;
  align-items: center;
  display: flex;
}

.box1, .box2 {
  height: 200px;
  width: 200px;
}

.box1 {
  background-color: purple;
  animation: 4s infinite forBox1;
}

.box2 {
  background-color: teal;
  animation: 4s infinite forBox2;
}

@keyframes forBox1 {
  0% {
    transform: translate(0%);
  }

  25% {
    transform: translate(0%, -100%);
  }

  50% {
    transform: translate(-100%, -100%);
  }

  75% {
    transform: translate(-100%);
  }

  100% {
    transform: translate(0%);
  }
}

@keyframes forBox2 {
  0% {
    transform: translate(0%);
  }

  25% {
    transform: translate(0%, -100%);
  }

  50% {
    transform: translate(100%, -100%);
  }

  75% {
    transform: translate(100%);
  }

  100% {
    transform: translate(0%);
  }
}

